}

.logo {
  color: #ae1332;
  fill: #ae1332;
  text-align: center;
  text-transform: uppercase;
  align-content: center;
  justify-content: center;
  gap: 8px;
  display: flex;
}

.logo__icon {
  fill: #ae1332;
}

.logo__name {
  color: #ae1332;
  letter-spacing: .04em;
  font-size: 14px;
  line-height: 1.2;
  text-decoration: none;
}

.js-menu-container {
  display: none;
}

.basket__icon {
  fill: #ae1332;
  padding: 11px 32px;
}

.menu-toggle {
  display: none;
}

.menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
}

@media screen and (max-width: 768px) {
  .nav {
    width: 768px;
  }

  .nav__list {
    gap: 16px;
  }

  .logo {
    padding: 17px 170px 0 0;
  }

  .menu, .menu-toggle {
    display: none;
  }
}

@media screen and (max-width: 376px) {
  .nav__list {
    padding: 0;
  }

  .js-menu-container {
    display: block;
  }

  .nav {
    width: 375px;
  }

  .nav__list {
    display: none;
  }

  .logo {
    padding: 15px 90px 0 0;
  }

  .logo__name {
    font-weight: 700;
  }

  .basket__icon {
    display: none;
  }

  .menu-toggle {
    min-height: 40px;
    min-width: 40px;
    fill: #ae1332;
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    border-radius: 50%;
    outline: none;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 10px 25px 0 0;
    display: flex;
  }

  .menu-container {
    width: 100vw;
    height: 100vh;
    z-index: 999;
    background-color: #ae1332;
    padding: 100px 32px;
    transition: transform .25s ease-in-out;
    position: fixed;
    top: 0;
    left: 0;
    transform: translateX(100%);
  }

  .menu-container.is-open {
    transform: translateX(0);
  }

  .menu-toggle.js-close-menu {
    cursor: pointer;
    fill: #ae1332;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    top: 10px;
    right: 10px;
  }

  .menu-container {
    color: #fff;
    fill: #fff;
    top: 50px;
    right: 16px;
  }

  .mobile-menu {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .mobile-menu .link {
    text-align: center;
    color: #fff;
    fill: #fff;
    padding: 10px;
    text-decoration: none;
    display: block;
  }
}

